<template>
  <div class="button">
    <div class="bt bt1" v-if="type == 'horizontal'">{{ title }}</div>
    <div class="bt bt2" v-if="type == 'vertical'">{{ title }}</div>
    <div class="bt bt3" v-if="type == 'obliqueLeft'">{{ title }}</div>
    <div class="bt bt4" v-if="type == 'obliqueRight'">{{ title }}</div>
  </div>
</template>
<script lang="ts">
import { defineComponent, PropType, ref } from "vue";
enum derectionType {
  horizontal,
  vertical,
  obliqueLeft,
  obliqueRight,
}
export default defineComponent({
  props: {
    title: {
      type: String,
      default: "hover me",
    },
    type: String,
    // type: {
    //   type: Enum as PropType<derectionType>,
    // },
  },
});
</script>
<style lang="scss" scoped>
.button {
  display: inline-block;
  margin: 0 10px;
  .bt {
    cursor: pointer;
    padding: 20px 60px;
    border: 2px solid #00cec9;
    position: relative;
    overflow: hidden;
    color: #fff;
    z-index: 1;
  }
  .bt::before {
    background-color: #00cec9;
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    transition: 0.3s;
  }
  .bt1::before {
    width: 0%;
    height: 100%;
  }
  .bt2::before {
    width: 100%;
    height: 0%;
  }
  .bt3::before {
    width: 100%;
    height: 0%;
    transform: translate(-50%, -50%) rotate(17deg);
  }
  .bt4::before {
    width: 100%;
    height: 0%;
    transform: translate(-50%, -50%) rotate(-17deg);
  }
  .bt:hover::before {
    width: 200%;
    height: 200%;
  }
}
</style>
